<template>
    <div class="footer">
        <div style="clear: both;font-size: 0px;height: 0px"></div>
        <div class="content">
            <a class="tel" href="tel:02784221111">电话咨询</a>
            <a class="weixin" href="http://www.yhcs.com/" @click="copyAddress('weixin')">进入官网</a>
            <input type="hidden" id="weixin" value="www.baidu.com">
        </div>
        <input type="text" name="copy" id="copy" style="display: none;">
    </div>
</template>
<style lang="scss">
    .footer {
        margin: 0.4rem 0;
        height: .8rem;
        .content {
            margin: 0 0 0.3rem 0.24rem;
            padding-bottom: 0.3rem;
            text-align: center;
            display: flex;
            .tel {
                width: 3.4rem;
                height: 0.8rem;
                line-height: 0.8rem;
                text-align: center;
                border: solid 1px #ffb900;
                color: #ffb900;
                border-radius: 0.5rem;
                font-size: 0.26rem;
                box-shadow: 0 0 .05rem #ffb900;
            }
            .weixin {
                width: 3.4rem;
                height: 0.8rem;
                line-height: 0.8rem;
                text-align: center;
                border: solid 1px #ffb900;
                color: #ffb900;
                border-radius: 0.5rem;
                font-size: 0.26rem;
                box-shadow: 0 0 .05rem #ffb900;
            }
        }
        .tel {
            margin-right: 0.22rem;
        }
    }
</style>
<script>
    import {Toast} from 'mint-ui';

    export default {
        data() {
            return {}
        },
        methods: {
            copyAddress(elementId) {
                var weChat = document.getElementById("copy");
                var content = document.getElementById(elementId).innerHTML || document.getElementById(elementId).value;
                weChat.setAttribute("value", content);
                weChat.select();
                document.execCommand("copy");
                Toast("复制成功")
            }
        },
    }
</script>



