                }
                .questioneftTxt {
                    float: left;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    width: 4.9rem;
                    font-size: 0.3rem;
                }
                div {
                    color: #666;
                    text-indent: 0.44rem;
                    line-height: 0.5rem;
                    font-size: 0.24rem;
                    background-color: #f5f5f5;
                    padding: 0.1rem;
                }
            }
        }
    }
    .gotop {
        position: fixed;
        bottom: 2.5rem;
        right: 0.2rem;
        border-radius: 2.5rem;
        width: 0.8rem;
        height: 0.8rem;
        background: url("./topIcon.png") no-repeat;
        background-size: 100%;
        z-index: 50;
        display: none;
    }
}

.gaps {
    width: 100%;
    height: 0.3rem;
    background: #eeeeee;
}

.carsArticalListTitle {
    height: 0.38rem;
    line-height: 0.38rem;
    margin: 0.3rem 0 0 0.4rem;
    font-size: 0.3rem;
    color: #000000;
    font-weight: bold;
    text-indent: 0.2rem;
    border-left: solid 0.08rem $orange;
}

@mixin homeBtn {
    display: block;
    height: 0.88rem;
    text-align: center;
    font-size: 0.3rem;
    line-height: 0.88rem;
    color: #f6a32b;
    border-bottom: solid 0.08rem #ddd;
}
</style>
<script>
import api from "../../utils/api";
import search from "../search/search.vue";
import vueLoading from "vue-loading-template";
import { Toast, Swipe, SwipeItem } from "mint-ui";
import Vue from "vue";
export default {
    data() {
        return {
            showAllCar: false,
            troubleDetail: false,
            changeNum: 1,
            troubles: [],
            articleList: [],
            articalPage: 1,
            articalRows: 3,
            showNum: 4,
            pageCount: "",
            counter: 0,
            recommends: [],
            s: [],
            customerId: "",
            tokens: "",
            historyVehicleId: [],
            curCity: "",
            showCenter: false,
            imgUrl: [],
            storage: "",
            isLoading: true,
            selected: "",
            area: [],
            sumStock: "",
            msgLists: "",
            brandTotal: [],
            brandList: [
                { minPrice: 0, maxPrice: 5, content: "5万以下" },
                { minPrice: 5, maxPrice: 10, content: "5-10万" },
                { minPrice: 10, maxPrice: 20, content: "10-20万" },
                { minPrice: 20, maxPrice: "", content: "20万以上" }
            ],
            carsType: [
                { sort: "准新车" },
                { sort: "练手车" },
                { sort: "超值SUV" },
                { sort: "小型车" }
            ],
            currentCitye: "",
            cityid: "",
            logined: false
        };
    },
    methods: {
        //功能暂未上线
        noLine() {
            Toast("功能暂未上线，敬请期待");
        },
        //加入看车单
        addCarList: function(recommend) {
            var _this = this;
            var storage = window.localStorage;
            if (recommend.is_look_over == true) {
                return;
            } else {
                if (storage.getItem("user")) {
                    if (recommend.is_in_look == 1) {
                        $.ajax({
                            type: "POST",
                            url: this.global.host + "watchList/remove",
                            data: {
                                token: JSON.parse(
                                    window.localStorage.getItem("user")
                                ).token,
                                vehicleId: recommend.vehicle_id
                            },
                            success: function(response) {
                                if (response.resultCode === "0000") {
                                    Toast("已移除看车单");
                                    recommend.is_in_look = 0;
                                } else if (response.resultCode === "1111") {
                                    Toast("操作失败");
                                }
                            }
                        });
                    } else {
                        $.ajax({
                            type: "post",
                            url: this.global.host + "/watchList/add",
                            data: {
                                token: JSON.parse(
                                    window.localStorage.getItem("user")
                                ).token,
                                vehicleId: recommend.vehicle_id
                            },
                            success: function(response) {
                                if (response.resultCode == "10086") {
                                    _this.errorAjax(response.resultMsg, _this);
                                } else {
                                    if (response.resultCode == "0000") {
                                        recommend.is_in_look = 1;
                                        Toast("成功加入看车单");
                                    } else if (response.resultCode == "1111") {
                                        Toast("加入看车单失败");
                                    }
                                }
                            }
                        });
                    }
                } else {
                    window.localStorage.setItem(
                        "item",
                        JSON.stringify(recommend)
                    );
                    _this.$router.push("/login");
                }
            }
        },
        gotop() {
            let $nav2Top = $(".gotop");
            $(window).scroll(function() {
                let scrollTop = $(this).scrollTop();
                let windowHeight = $(this).height();
                if (scrollTop >= windowHeight) {
                    $nav2Top.show();
                } else {
                    $nav2Top.hide();
                }
            });
        },
        scrolltop() {
            $(window).scrollTop(0);
        },
        troubleShow(item) {
            if (item.show == undefined) {
                Vue.set(item, "show", false);
            }
            item.show = !item.show;
            $("html,body").animate(
                { scrollTop: document.body.scrollHeight },
                1000
            );
        },
        updateCars() {
            var lens = this.recommends.length;
            var pageNum = Math.floor(lens / this.showNum);
            if (this.counter === pageNum - 1) {
                this.counter = 0;
            } else {
                this.counter++;
            }
        },
        //加入历史浏览
        saveHistory(recommend) {
            var Storage = window.localStorage;
            var ifHas = Storage.getItem("History");
            if (ifHas) {
                var newStorage = JSON.parse(ifHas);
                if (newStorage.length == 50) {
                    newStorage.pop();
                    newStorage.unshift(recommend);
                    var arrTable = {},
                        arrData = [];
                    for (var i = 0; i < newStorage.length; i++) {
                        if (!arrTable[newStorage[i].vehicle_id]) {
                            arrTable[newStorage[i].vehicle_id] = true;
                            arrData.push(newStorage[i]);
                        }
                    }
                    Storage.setItem("History", JSON.stringify(arrData));
                } else {
                    newStorage.unshift(recommend);
                    var arrTable = {},
                        arrData = [];
                    for (var i = 0; i < newStorage.length; i++) {
                        if (!arrTable[newStorage[i].vehicle_id]) {
                            arrTable[newStorage[i].vehicle_id] = true;
                            arrData.push(newStorage[i]);
                        }
                    }
                    Storage.setItem("History", JSON.stringify(arrData));
                }
            } else {
                this.s.unshift(recommend);
                Storage.setItem("History", JSON.stringify(this.s));
            }
        },
        checkLogin() {
            this.$router.push("/personal");
        },
        cityChoice(event) {
            this.isLoading = true;
            var storage = window.localStorage;
            var cityVaule = event.target.value;
            this.currentCitye = cityVaule;
            if (!storage.getItem("city")) {
                storage.setItem("city", JSON.stringify(cityVaule));
            } else {
                if (storage.getItem("city") == cityVaule) {
                    return;
                } else {
                    storage.setItem("city", JSON.stringify(cityVaule));
                }
            }
            this.requestCity();
            this.requestSlideList();
            if (window.localStorage.getItem("History")) {
                this.getRecommend();
            } else {
                this.getRecommendNo();
            }
        },
        shouYe() {
            window.location.reload();
        },
        // 请求品牌
        requestCity() {
            var $this = this;
            $.ajax({
                type: "POST",
                url: this.global.host + "stats/brandsCount",
                data: {
                    city: $this.currentCitye
                },
                success: function(response) {
                    if (response.resultCode == "10086") {
                        // $this.errorAjax(response.resultMsg, $this);
                        alert("获取品牌");
                    } else {
                        if (response.resultCode === "0000") {
                            $this.brandTotal = response.result.status_stock;
                            $this.sumStock = response.result.all_count;
                            if ($this.sumStock > 100) {
                                $this.showAllCar = true;
                            } else {
                                $this.showAllCar = false;
                            }
                        } else {
                        }
                    }
                }
            });
        },
        //请求轮播图
        requestSlideList() {
            var _this = this;
            $.ajax({
                type: "GET",
                url: _this.global.host + "slideshow/getSlideshowBOList",
                data: {},
                success: function(response) {
                    if (response.resultCode == "10086") {
                        // _this.errorAjax(response.resultMsg, _this);
                        alert("获取轮播图");
                    } else {
                        _this.imgUrl = response.result;
                    }
                }
            });
        },
        getRecommend() {
            var _this = this;
            $.ajax({
                type: "POST",
                url: this.global.host + "inventory/homeRecommend",
                data: {
                    token: _this.tokens,
                    locationCity: JSON.parse(
                        window.localStorage.getItem("city")
                    ),
                    vehicleIds: JSON.stringify(_this.historyVehicleId)
                },
                success: function(response) {
                    _this.isLoading = false;
                    if (response.resultCode == "10086") {
                        _this.errorAjax(response.resultMsg, _this);
                    } else {
                        if ((response.resultCode = "0000")) {
                            _this.recommends = response.result.list;
                        } else {
                            console.log("推荐没数据！");
                        }
                    }
                }
            });
        },
        getRecommendNo() {
            var _this = this;
            $.ajax({
                type: "POST",
                url: this.global.host + "inventory/homeRecommend",
                data: {
                    token: _this.tokens,
                    locationCity: JSON.parse(
                        window.localStorage.getItem("city")
                    ),
                    vehicleIds: JSON.stringify([])
                },
                success: function(response) {
                    _this.isLoading = false;
                    if (response.resultCode == "10086") {
                        _this.errorAjax(response.resultMsg, _this);
                    } else {
                        if (response.resultCode == "0000") {
                            _this.isLoading = false;
                            _this.recommends = response.result.list;
                        } else {
                            _this.recommends = response.result.list;
                        }
                    }
                }
            });
        }
    },
    mounted() {
        var historyCars = JSON.parse(window.localStorage.getItem("History"));
        var storage = window.localStorage;
        var $this = this;
        window.addEventListener("scroll", this.gotop);
        api.getCity().then(res => {
            //获取城市列表
            this.area = res.result.marketCityAll;
            this.selected = res.result.marketCityAll[0].cityid;
            this.requestCity();
            this.requestSlideList();
        });
        // $.ajax({
        //   type: "POST",
        //   url: $this.global.host + "city/getMarketCity",
        //   data: {},
        //   success: function(response) {
        //     if (response.resultCode === "10086") {
        //       $this.errorAjax(response.resultMsg, $this);
        //     }
        //     if (response.resultCode === "0000") {
        //       $this.area = response.result.marketCityAll;
        //       $this.currentCitye = response.result.marketCityAll[0].cityid;
        //       if (window.localStorage.getItem("city")) {
        //         $this.selected = JSON.parse(window.localStorage.getItem("city"));
        //         console.log($this.selected);
        //         $this.currentCitye = $this.selected;
        //         $this.requestCity();
        //         $this.requestSlideList();
        //       } else {
        //         window.localStorage.setItem("city", $this.currentCitye);
        //         $this.selected = $this.currentCitye;
        //         $this.requestCity();
        //         $this.requestSlideList();
        //       }
        //     }
        //   }
        // });
        //    判断是否有登录状态
        if (!storage.getItem("user")) {
            this.logined = false;
        } else {
            if (window.localStorage.getItem("item")) {
                $this.addCarList(
                    JSON.parse(window.localStorage.getItem("item"))
                );
                window.localStorage.setItem("item", "");
            } else {
            }
            this.logined = true;
        }
        $.ajax({
            type: "GET",
            url: $this.global.host + "article/getArticlePageList",
            data: {
                page: 1,
                rows: 3
            },
            success: function(response) {
                if (response.resultCode === "10086") {
                    $this.errorAjax(response.resultMsg, $this);
                }
                if (response.resultCode === "0000") {
                    $this.articleList = response.result.rows;
                    $this.articleList.slice(0, 4);
                    let len = $this.articleList.length;
                    if (len < 3) {
                        $(".moreCars").hide();
                    }
                    if (len < 1) {
                        $(".carsArtical").hide();
                    }
                } else {
                }
            }
        });
        $.ajax({
            type: "POST",
            url: $this.global.host + "faq/getFaqPageList",
            data: {
                page: $this.articalPage,
                rows: $this.articalRows
            },
            success: function(response) {
                if (response.resultCode === "10086") {
                    $this.errorAjax(response.resultMsg, $this);
                }
                if (response.resultCode === "0000") {
                    console.log(response);
                    $this.troubles = response.result.rows;
                }
            }
        });
        //    有无历史记录的判断
        setTimeout(function() {
            if (historyCars) {
                historyCars.forEach(function(item) {
                    $this.historyVehicleId.push(item.vehicle_id);
                });
                $this.historyVehicleId = $this.historyVehicleId.slice(0, 10);
                $this.getRecommend();
            } else {
                $this.getRecommendNo();
            }
        }, 100);
    },
    computed: {
        filterCars: function() {
            var counter = this.counter;
            var recommends = this.recommends;
            return recommends.filter(function(recommend, index) {
                return Math.floor(index / 4) === counter;
            });
        }
    },
    components: {
        vueLoading
    }
};
Vue.filter("mainTime", function(value) {
    //value为13位的时间戳
    function addt(m) {
        return m < 10 ? "0" + m : m;
    }
    var time = new Date(parseInt(value));
    var y = time.getFullYear();
    var m = time.getMonth() + 1;
    var d = time.getDate();

    return y + "年" + addt(m) + "月";
});
</script>



